
.header-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 20px;
    border-bottom: solid 1px white;
    margin-top: 8%;
    padding-bottom: 5%;
}

.menu-button a {
    color: white;
    text-decoration: none;
}

.chevron-arrow-left {
    display: inline-block;
    border-right: 4px solid white;
    border-bottom: 4px solid white;
    width: 10px;
    height: 10px;
    transform: rotate(-225deg);
}

.coins {
    display: flex;
    flex-direction: row;
    text-decoration: none;
    color: white;
}

.coins img {
    width: 20px;
}

.gamemodes-container {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    display: flex;
    margin: 7% 4% 4%;
}

.gamemodes-container a {
    max-width: 30%;
    border-radius: 5%;
    text-align: center;
    min-width: 125px;
    min-height: 125px;
    margin-left: 3%;
    margin-right: 3%;
    margin-bottom: 16%;
}

.gamemodes-container img {
    width: 100%;
}

.bottom-container {
    width: 100%;
    height: 15%;
    flex-direction: row;
    display: flex;
    justify-content: flex-start;
}

.bottom-container div {
    min-width: 20%;
    border-radius: 5%;
}

.container .coins img {
    max-height: 20.25px;
    margin-right: 3%;
}

.container .coins {
    align-items: center;
}
